.gallery {
  h2 {
    margin-bottom: 40px;
  }
}

.gallery__wrap {
  display: grid;
  grid-template-columns: 1.32fr 1fr 1fr;
  gap: 20px;

  @media (max-width: $viewport-s) {
    grid-template-columns: 1fr 1fr;
    gap: 15px;
  }
}

.gallery__wrap-img {
  &:nth-child(6n + 1) {
    grid-row: span 2;

    @media (max-width: $viewport-s) {
      grid-row: initial;
      grid-column: span 2;
    }
  }

  &:nth-child(4n + 4) {
    grid-column: span 2;
  }
}
