@import "variables";
@import "mixins";
@import "fonts";
@import "global";
@import "animation";

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

*:focus:not(:focus-visible) {
  outline: none;
}

:root {
  --offset-page: 30px;
  --header-height: 84px;

  @media (max-width: $viewport-m) {
    --header-height: 68px;
  }

  @media (max-width: $viewport-s) {
    --offset-page: 16px;
    --header-height: 56px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: calc(1140px + var(--offset-page) * 2);
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--offset-page);
}

.content-img {
  height: 100%;
  object-fit: cover;
  object-position: 50% 0;
  //background-color: $bg-img;
}

.content {
  position: relative;
  max-width: 100%;
  transition: max-width $duration-default;
}

.logo {
  width: 164px;
  height: 25px;
  flex-shrink: 0;
  color: white;
  transition: color $duration-default;

  svg {
    pointer-events: none;
  }

  @media (max-width: 1100px) {
    width: 124px;
    height: 18px;
  }

  @media (max-width: $viewport-s) {
    width: 110px;
    height: 16px;
  }

  &:hover,
  &:focus {
    color: $color-blue;
  }
}

.btn {
  padding: 16px 32px;
  flex-shrink: 0;
  font-weight: 600;
  color: white;
  border: 2px solid $color-blue;
  text-align: center;

  @media (max-width: $viewport-m) {
    padding: 10px 22px;
  }

  @media (max-width: $viewport-s) {
    width: 100%;
    //max-width: 350px;
    margin: 0 auto;
  }

  &--map {
    @include anim-shutter();

    @media (max-width: $viewport-m) {
      text-transform: uppercase;
    }
  }

  &--blue {
    background: $color-blue;
    transition: background-color $duration-default, border-color $duration-default;

    &:hover,
    &:focus {
      border-color: $color-blue-hover;
      background: $color-blue-hover;
    }
  }

  &:active {
    background: $color-blue-active;
  }
}

.title {
  margin-bottom: 32px;
  font-weight: 600;
  font-size: 40px;
  line-height: 1.3;
  color: $font-black;
  max-width: 770px;

  @media (max-width: $viewport-m) {
    max-width: initial;
  }

  @media (max-width: $viewport-s) {
    margin-bottom: 24px;
    font-size: 28px;
  }
}

.title-border {
  position: relative;
  padding-left: 28px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 4px;
    //height: 52px;
    background: $color-blue;
  }

  @media (max-width: $viewport-s) {
    padding-left: 0;

    &::before {
      content: none;
    }
  }
}

.block-top-offset {
  padding-top: 160px;
  padding-bottom: 160px;

  @media (max-width: $viewport-m) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (max-width: $viewport-s) {
    padding-top: 72px;
    padding-bottom: 72px;
  }
}

.accordion__title {
  transition: color $duration-default;

  &::before,
  &::after {
    top: 8px;
  }

  &:hover,
  &:focus {
    color: $color-blue-hover;

    &::before,
    &::after {
      background-color: $color-blue-hover;
    }
  }
}

.accordion__item {
  &.active {
    .symbol-corner {
      &::before {
        transform: translate(-2px) rotate(-45deg);
      }

      &::after {
        transform: translate(2px) rotate(45deg);
      }
    }

    .symbol-cross {
      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: rotate(-45deg);
      }
    }
  }
}

.symbol-cross {
  &::before,
  &::after {
    content: "";
    position: absolute;
    right: 5px;
    width: 14px;
    height: 2px;
    border-radius: 5px;
    background-color: black;
    transition: transform $duration-default;
  }

  &::before {
    transform: rotate(90deg);
  }

  &::after {
    transform: rotate(0deg);
  }
}

.symbol-corner {
  &::before,
  &::after {
    content: "";
    position: absolute;
    right: 16px;
    width: 6px;
    height: 2px;
    transition: transform $duration-default ease-out, background-color $duration-default;
    background-color: currentColor;
    transform: translate(-2px) rotate(45deg);
  }

  &::after {
    transform: translate(2px) rotate(-45deg);
  }
}

#spinner {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: calc(100 * var(--vh, 1vh));
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .5);

  &.hidden {
    display: none;
  }

  &::after {
    content: "";
    width: 120px;
    height: 120px;
    background: url("../img/spinner.svg") center/cover;
  }
}

.burger-menu__list,
.specifications__list,
.header__nav-list,
.footer__nav-list,
.dealers__list,
.social-icons__list,
.documentation__list,
.accordion,
.stable__list,
.info__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.gm-style-iw-d {
  h3 {
    margin-bottom: 16px;
    font-family: $font-default;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    color: $color-blue;
  }

  p, a {
    font-family: $font-default;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: .04em;
    color: $color-gray;
  }

  a {
    display: block;
    transition: color $duration-default;

    &:hover,
    &:focus {
      color: $color-blue;
    }
  }

  @media (max-width: $viewport-s) {
    h3 {
      margin-bottom: 10px;
      font-size: 18px;
    }

    p {
      font-size: 14px;
    }
  }

  @media (max-width: 350px) {
    h3 {
      margin-bottom: 5px;
      font-size: 14px;
    }

    p {
      font-size: 14px;
    }
  }
}

.gm-style .gm-style-iw-c {
  padding: 24px 16px;
  border-radius: 0;

  @media (max-width: $viewport-s) {
    padding: 12px;
  }
}

.marked-list-blue {
  counter-reset: blue-number;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    letter-spacing: .015em;
    color: #1D1D1D;

    @media (max-width: $viewport-s) {
      font-size: 16px;
    }

    &::before {
      content: counter(blue-number);
      counter-increment: blue-number;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      width: 72px;
      height: 72px;
      margin-right: 28px;
      font-size: 40px;
      color: white;
      background-color: $color-blue;
      border-radius: 50%;

      @media (max-width: $viewport-s) {
        width: 56px;
        height: 56px;
        margin-right: 16px;
        font-size: 32px;
      }
    }
  }

  li + li {
    margin-top: 40px;
  }
}

.map-container {
  position: relative;
  background-color: $bg-img;
  z-index: 0;

  @media (max-width: $viewport-m) {
    width: 100%;
    height: 0;
    padding-top: Max(400px, 58.66%);
  }

  &::before {
    content: "ЗАГРУЖАЕТСЯ КАРТА...";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    font-size: 30px;
    text-align: center;
    z-index: -1;
  }
}

.cookies {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 332px;
  padding: 24px 48px 24px 16px;
  font-size: 14px;
  background: white;
  color: $color-gray;
  line-height: 1.3;
  visibility: visible;
  opacity: 1;
  transition: visibility .6s, opacity .6s;
  z-index: 6;

  &.hidden {
    visibility: hidden;
    opacity: 0;
  }

  @media (max-width: 700px) {
    width: 100%;
  }

  button {
    position: absolute;
    top: 17px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    svg {
      width: 15px;
      height: 15px;
    }
  }
}
