.aerotex {
  background: $color-graphite;
  color: white;

  h2 {
    color: white;

    @media (max-width: $viewport-m) {
      display: none;
    }
  }
}

.aerotex__wrap {
  display: flex;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 120px;

  @media (max-width: $viewport-m) {
    flex-direction: column;
    padding-top: 72px;
    padding-bottom: 72px;
  }
}

.mobile-title {
  display: none;

  @media (max-width: $viewport-m) {
    display: block;
    width: 100%;
    color: white;
  }
}

.aerotex__content {
  margin-right: 24px;
  font-size: 20px;

  p + p {
    margin-top: 30px;
  }

  @media (max-width: $viewport-m) {
    font-size: 18px;
    order: 1;
    margin: 32px 0 0;
  }
}

.aerotex__wrap-img {
  width: 33%;
  flex-shrink: 0;

  video {
    width: 100%;
    height: 100%;
  }

  @media (max-width: $viewport-m) {
    width: 100%;
  }
}
