.specifications-wrap {
  max-width: 1300px;
  padding: 50px;
  margin: 0 auto;

  @media (max-width: $viewport-m) {
    padding: 50px 30px;
  }

  @media (max-width: $viewport-s) {
    padding: 0 0 80px 0;
  }
}

.specifications {
  h2 {
    margin-bottom: 64px;
    font-weight: 600;
    font-size: 48px;

    .short {
      display: none;
    }

    @media (max-width: $viewport-m) {
      font-size: 34px;
    }

    @media (max-width: $viewport-s) {
      padding: 16px 50px 16px 16px;
      font-size: 24px;
      border-bottom: 1px solid rgba(#3C3C3B, .1);

      .short {
        display: inline;
      }

      .long {
        display: none;
      }
    }
  }
}

.specifications__block {
  display: grid;
  grid-template-columns: 1.3fr 4fr;
  gap: 30px;

  @media (max-width: $viewport-m) {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  @media (max-width: $viewport-s) {
    padding: 0 16px;
  }

  &--basic {
    margin-bottom: 56px;

    @media (max-width: $viewport-s) {
      margin-bottom: 64px;
    }
  }
}

.specifications__title {
  h3 {
    font-weight: normal;
    font-size: 24px;
    line-height: 1;
    color: #000;

    @media (max-width: $viewport-s) {
      font-size: 20px;
    }
  }
}

.specifications__list {
  display: grid;
  grid-template-columns: .9fr 1.6fr;
  line-height: 1;
  color: #000;
  gap: 24px;

  @media (max-width: $viewport-s) {
    grid-template-columns: 1fr;
    gap: 32px;
  }

  &--basic {
    font-size: 14px;
  }
}

.specifications__term {
  margin-bottom: 8px;
  font-weight: 600;
}
