.feature__form {
  display: flex;
  max-width: 380px;
  flex-direction: column;
  margin: 0 auto;
}

.feature__title {
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 40px;
  line-height: 1;

  @media (max-width: $viewport-m) {
    font-size: Max(Min(3.9vw, 40px), 24px);
  }
}

.feature__desc {
  margin-bottom: 32px;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: .015em;
  color: $font-light-black;

  @media (max-width: $viewport-s) {
    font-size: 16px;
  }
}

.feature__fields {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 32px;
  border: none;

  b {
    width: 100%;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 16px;
    line-height: 1;
    letter-spacing: .015em;
  }

  & > div {
    position: relative;
    display: flex;
    font-size: 18px;
    line-height: 1;
    cursor: pointer;

    &:not(:last-of-type) {
      margin-right: 9px;
    }
  }
}

.feature__options {
  padding: 10px 18px;
  background-color: #F4F4F4;
  color: $font-light-black;
  transition: background-color $duration-default, color $duration-default;

  &:hover,
  &:focus {
    background-color: $color-blue-hover;
    color: white;
  }

  &.active {
    background-color: $color-blue;
    color: white;
  }
}

.feature__options-color {
  &:hover,
  &:focus {
    box-shadow: 0 0 0 2px #FFF, 0 0 0 4px #CFCFCF;
    background: $font-black;
  }

  &.active {
    box-shadow: 0 0 0 2px #FFF, 0 0 0 4px #000;
  }
}

.feature__link {
  font-weight: 600;
  line-height: 1;
  letter-spacing: .015em;
  color: $color-blue;

  &:hover,
  &:focus {
    color: $color-blue-hover;
  }
}

.feature .accordion {
  margin-bottom: 20px;
}

.feature .accordion__item {
  margin-top: 20px;
  border: none;
}

.feature__total {
  margin: 32px 0 16px;
  font-size: 32px;
  font-weight: bold;
  line-height: 1;
}

.feature__btn {
  padding: 16px 48px;
  margin-right: auto;
  text-transform: uppercase;
}

.feature__list {
  padding-left: 10px;
  margin: 0;
  color: #3C3C3B;
  line-height: 1.2;

  li + li {
    margin-top: 16px;
  }

  li::marker {
    color: currentColor;
  }
}

.feature .feature__options-color {
  width: 32px;
  height: 32px;
  background: $font-black;
  border-radius: 50%;
  transition: box-shadow $duration-default;
  padding: 0;
}

.feature__model-name {
  font-size: 14px;

  & > span {
    font-weight: 600;
    color: #000;
    text-transform: uppercase;
  }
}
