.contact {
  display: flex;
  min-height: calc(100 * var(--vh, 1vh) - var(--header-height));

  @media (max-width: $viewport-m) {
    flex-direction: column;
  }
}

.contact__content,
.contact__map {
  width: 50%;

  @media (max-width: $viewport-m) {
    width: 100%;
  }
}

.contact__content {
  padding: 80px 0;
}

.contact__inner {
  width: 415px;
  margin: 0 auto;

  @media (max-width: $viewport-m) {
    width: 100%;
    padding: 0 16px;
  }

  h1 {
    margin-bottom: 58px;
  }
}

.contact__link-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
  font-weight: 600;
  font-size: 20px;
  color: #000;

  & > a + a {
    margin-left: 20px;
  }

  @media (max-width: $viewport-m) {
    flex-direction: column;
    align-items: flex-start;

    & > a + a {
      margin: 24px 0 0;
    }
  }
}

.contact__link {
  display: flex;
  align-items: center;
  transition: color $duration-default;

  svg {
    width: 24px;
    height: 24px;
    margin-right: 12px;
  }

  &:hover,
  &:focus {
    color: $color-blue-hover;
  }
}

.contact__phone {
  &:hover svg {
    animation-name: buzz-out;
    animation-duration: .4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}

.contact__mail {
  &:hover,
  &:focus {
    svg {
      animation-name: tada;
      animation-duration: 1s;
      animation-timing-function: ease-in-out;
    }
  }
}

.contact__list {
  margin-bottom: 40px;

  & > div + div {
    margin-top: 40px;
  }
}

.contact__list-item {
  dt {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 20px;
    color: #000;
  }

  dd {
    color: $color-gray;
  }
}

.contact__requisite {
  display: inline-block;
  margin-bottom: 48px;
  font-weight: 600;
  font-size: 20px;
  color: $color-blue;
}

.contact__social-title {
  margin-bottom: 13px;
  font-weight: 600;
  font-size: 20px;
  color: #000;
}
