.burger-menu {
  --font-size: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100 * var(--vh, 1vh));
  transform: translateY(-100px);
  max-height: 0;
  display: grid;
  background: $color-graphite;
  visibility: hidden;
  transition-property: transform, max-height, visibility;
  transition-duration: .6s;
  transition-timing-function: $timing-default;
  //overflow: hidden;
  overflow-y: scroll;
  z-index: 2;

  @media (max-width: 1100px) {
    --font-size: 18px;
  }

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.nav-menu-active .burger-menu {
  max-height: calc(150 * var(--vh, 1vh));
  transform: translateY(0);
  visibility: visible;
}

.burger-menu__wrap {
  display: flex;
}

.burger-menu__left,
.burger-menu__content {
  padding: 202px 0 64px 0;
}

.burger-menu__left.active {
  .burger-menu__link--models {
    color: white;
  }

  & + .burger-menu__content {
    opacity: 1;
  }
}

.burger-menu__content {
  opacity: 0;
  transition: opacity $duration-default;
}

.burger-menu__left {
  max-width: 340px;
  flex-grow: 1;
  font-weight: 600;
  font-size: 18px;
  border-right: 1px solid $color-gray;

  .social-icons {
    margin-top: 108px;
    padding-right: 20px;
  }
}

.burger-menu__list {
  position: relative;
  width: 100%;
  padding-right: 15%;
  display: grid;
  gap: 48px;
}

.menu-pointer {
  position: absolute;
  top: 8px;
  right: -12px;
  width: 24px;
  height: 24px;
  border-left: 1px solid #FFF;
  border-bottom: 1px solid #FFF;
  transform: rotate(45deg);
  transition: transform $duration-default;
  background: $color-graphite;

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.burger-menu__link {
  display: block;
  color: $color-menu-item;
  transition: color $duration-default;

  &:hover,
  &:focus {
    color: white;
  }
}

.content-menu {
  display: flex;
  padding-left: 69px;

  @media (max-width: $viewport-m) {
    display: none;
  }
}

.content-menu__left {
  margin-right: 56px;
}

.content-menu__list {
  display: flex;
  max-width: 293px;
  flex-direction: column;

  dd + dt {
    margin-top: 56px;
  }
}

.content-menu__term {
  margin-bottom: 32px;
  font-weight: 600;
  //font-size: calc(var(--font-size) + 4px);
  font-size: var(--font-size);
  line-height: 1.3;
}

.content-menu__def {
  font-size: var(--font-size);
  text-transform: uppercase;

  a + a {
    margin-top: 20px;
  }

  a {
    display: block;
    transition: color $duration-default;

    &:hover,
    &:focus {
      color: $color-blue;
    }
  }
}
