.requisites {
  padding: 56px;

  h2 {
    margin-bottom: 85px;
  }

  @media (max-width: $viewport-s) {
    padding: 16px 0 50px;

    h2 {
      margin-bottom: 60px;
      padding: 0 60px 16px 16px;
      border-bottom: 1px solid rgba(#3C3C3B, .1);
    }
  }
}

.requisites__list-wrap {
  margin-left: 72px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @media (max-width: $viewport-m) {
    margin-left: 0;
  }

  @media (max-width: $viewport-s) {
    padding: 0 16px;
    grid-template-columns: 1fr;
  }
}

.requisites__list {
  display: grid;
  gap: 40px;

  dt {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 20px;
    color: #000;
  }

  dd {
    color: $color-gray;
  }
}
