.modal {
  contain: strict;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: calc(100 * var(--vh, 1vh));
  overflow-y: scroll;
  visibility: hidden;
  z-index: 99;
  scrollbar-width: none;
  -ms-overflow-style: none;
  transform: translate3d(100%, 0, 0);
  transition-property: visibility, transform;
  transition-duration: .4s;
  transition-timing-function: ease-out;

  &::-webkit-scrollbar {
    display: none;
  }

  &.active {
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

.scale-in {
  opacity: 0;
  transition-property: opacity, visibility, transform;
  transition-duration: .3s;
  transform: scale3d(.5, .5, 1);

  &.active {
    transform: scale3d(1, 1, 1);
    visibility: visible;
    opacity: 1;
  }
}

.bounce-in-right {
  --bounceInRight-duration: .6s;
  opacity: 0;
  transform: translate3d(100%, 0, 0);

  &.active {
    animation-name: bounceInRight;
    animation-duration: var(--bounceInRight-duration);
    animation-fill-mode: both;
    animation-timing-function: cubic-bezier(.215, .61, .355, 1);
    //animation-timing-function: ease-out;
  }

  &.bounce-out-right {
    animation-name: bounceOutRight;
    animation-duration: .6s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
  }
}

.modal-wrap {
  padding-left: 8.33%;
  width: 100%;
  display: flex;
  justify-content: center;

  @media (max-width: $viewport-s) {
    padding: 0;
  }
}

.modal-inner {
  position: relative;
  display: flex;
  flex-grow: 1;
  background-color: white;
  min-height: calc(100 * var(--vh, 1vh));
}

.modal-overlay {
  position: fixed;
  visibility: hidden;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition-property: visibility, opacity;
  transition-duration: .15s;
  background-color: black;
  z-index: 98;

  &--show {
    opacity: .6;
    visibility: visible;
  }
}

.scroll-lock {
  width: 100%;
  position: fixed;
  overflow-y: scroll;
}

.modal-close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: opacity $duration-default;

  &--requisites {
    top: 60px;
  }

  &--dealers {
    top: 62px;

    @media (max-width: $viewport-m) {
      top: 1.5%;
    }
  }

  @media (max-width: $viewport-s) {
    top: 15px;
    right: 10px;

    &--dealers {
      top: 8px;
    }
  }

  svg {
    width: 24px;
    height: 24px;

    @media (max-width: $viewport-s) {
      width: 18px;
      height: 18px;
    }
  }

  &:hover,
  &:focus {
    opacity: .6;
  }
}
