.not-found {
  display: grid;
  height: calc(100 * var(--vh, 1vh) - var(--header-height));
  place-items: center;
  background-color: #212429;
  background-image:
    linear-gradient(rgba(#212429, .9), rgba(#212429, .9)),
    url('../img/content/station1.png');
  background-repeat: no-repeat;
  background-position: center;

  h1 {
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 180px;
    line-height: 1;
    letter-spacing: .02em;

    @media (max-width: $viewport-s) {
      font-size: 120px;
    }
  }
}

.not-found__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 490px;
  padding: 0 16px;
  color: white;

  p {
    text-align: center;
  }
}

.not-found__link {
  display: inline-block;
  margin: 48px auto 0;
}
