.dealers {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 56px 30px 0 0;

  @media (max-width: $viewport-m) {
    padding: 16px 0 0;
  }
}

.dealers__title {
  display: flex;
  align-items: center;
  margin: 0 0 40px 56px;
  font-weight: 600;
  font-size: 48px;
  line-height: 1;
  color: $font-black;

  h2 {
    font-weight: 600;
    font-size: inherit;
    line-height: 1;
    margin: 0 14px 0 0;
  }

  @media (max-width: 1230px) {
    font-size: Max(Min(48px, 3.5vw), 24px);
  }

  @media (max-width: 1100px) {
    font-size: Max(Min(48px, 3.5vw), 24px);
  }

  @media (max-width: $viewport-m) {
    margin: 0 0 40px 30px;
  }

  @media (max-width: $viewport-s) {
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 17px 16px;

    h2 {
      margin: 0 0 20px 0;
    }
  }

  @media (max-width: 375px) {
    font-size: 20px;
  }
}

.dealers__container {
  display: flex;
  flex-grow: 1;

  @media (max-width: $viewport-m) {
    flex-direction: column;
  }
}

.dealers__map {
  @media (min-width: $viewport-m + 1) {
    //height: 756px;
    width: 55%;
  }
}

.dealers__item {
  padding: 32px 30px;
  border-bottom: 1px solid #F0F2F4;

  @media (max-width: $viewport-s) {
    padding: 40px 16px;
  }

  h3 {
    position: relative;
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 24px;
    line-height: 1;
    color: #000;
    transition: color $duration-default;

    @media (max-width: $viewport-m) {
      font-size: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -12px;
      width: 4px;
      height: 24px;
      background-color: transparent;
      transition: background-color $duration-default;
    }
  }

  p, a {
    letter-spacing: .04em;
    color: $color-gray;
  }

  a {
    display: block;
    transition: color $duration-default;

    &:hover,
    &:focus {
      color: $color-blue;
    }
  }

  &.hover {
    h3 {
      color: $color-blue;

      &::before {
        background-color: $color-blue;
      }
    }
  }
}

.dealers-stage {
  background: $bg-gray;

  h2 {
    margin-bottom: 98px;

    @media (max-width: $viewport-s) {
      margin-bottom: 40px;
    }
  }
}

.dealers-stage__wrap {
  display: grid;
}

.dealers-stage__btn {
  display: inline-block;
  margin: 72px auto 0;
}
