.doc-page {
  padding-top: 80px;
  padding-bottom: 80px;

  .title {
    margin-bottom: 88px;

    @media (max-width: $viewport-s) {
      margin-bottom: 72px;
    }
  }
}

.accordion__title--doc {
  width: 100%;
  margin-bottom: 64px;
  padding: 0 50px 0 0;
  display: flex;
  flex-direction: column;

  &::before,
  &::after {
    width: 12px;
    transform: translate(-4px) rotate(45deg);
  }

  &::after {
    transform: translate(4px) rotate(-45deg);
  }
}

.accordion__content--doc {
  padding-bottom: 0;
}

.accordion__item {
  &.active {
    .accordion__content {
      max-height: 100vh;
    }

    .accordion__content--doc {
      padding-bottom: 80px;
    }

    .accordion__title--doc {
      &::before,
      &::after {
        transform: translate(-4px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(4px, -50%) rotate(45deg);
      }
    }
  }
}

.doc-page__title-item {
  min-height: 32px;
  margin-bottom: 16px;
  padding-left: 8px;
  font-weight: 600;
  font-size: 32px;
  line-height: 1;
  border-left: 4px solid $color-blue;

  @media (max-width: $viewport-s) {
    font-size: 24px;
  }
}

.doc-page__desc {
  color: $color-gray;
}
