.product-feature {
  display: grid;
  min-height: calc(100 * var(--vh, 1vh) - var(--header-height));
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  //padding: 80px 0 80px 0;

  &--models {
    min-height: initial;
    padding: 80px 0 80px 0;
  }

  &--long {
    padding-top: 0;
    padding-bottom: 0;

    .feature {
      @media (min-width: $viewport-s + 1) {
        grid-area: 1 / 1 / 2 / 2;
      }
    }
  }

  @media (max-width: $viewport-m) {
    column-gap: 0;
    grid-template-columns: 50% 50%;
  }

  @media (max-width: $viewport-s) {
    grid-template-columns: initial;
    gap: 20px;
  }
}

.feature--slider {
  --form-padding: 140px;
  padding: var(--form-padding) 0 var(--form-padding) 0;

  @media (max-width: $viewport-s) {
    --form-padding: 80px;
  }
}

.feature--models {
  @media (max-width: $viewport-m) {
    padding: 0 16px;
  }
}

.feature__form {
  position: sticky;
  top: calc(var(--header-height) + var(--form-padding));
}

.product-feature__img {
  position: relative;
  height: 100%;
  //padding-top: 111%;
  overflow: hidden;

  @media (max-width: $viewport-s) {
    min-height: calc(100 * var(--vh, 1vh) - var(--header-height));
  }
}

.product-feature__img-models {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.product-feature__img-wrap {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  height: 100%;
  width: 100%;
  color: white;
}

.product-feature__img-wrap-models {
  display: flex;
  width: 100%;
  color: white;
}

.slider {
  display: grid;
  gap: 150px;
  width: 100%;

  @media (max-width: $viewport-s) {
    //display: flex;
    //flex-direction: column;
    height: calc(100 * var(--vh, 1vh) - var(--header-height));
    scroll-snap-type: y mandatory;
    gap: 100px;
    overflow-y: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &.no-event {
    pointer-events: none;
  }
}

.slider__item {
  height: calc(100 * var(--vh, 1vh) - 140px);

  @media (max-width: $viewport-s) {
    height: auto;
    flex-shrink: 0;
    scroll-snap-align: start;
  }

  img {
    object-fit: contain;
    object-position: center;
  }
}

.pagination {
  position: absolute;
  top: 50%;
  left: 0;
  display: grid;
  gap: 16px;
  transform: translateY(-50%);

  span {
    width: 12px;
    height: 12px;
    background-color: #E1E1E1;
    border-radius: 50%;
    cursor: pointer;
  }

  .swiper-pagination-bullet-active {
    background-color: $color-blue;
  }
}

.accordion__title--corner {
  //width: 100%;
  min-width: 258px;
  padding: 0 40px 0 0;
  font-weight: 600;
  letter-spacing: .015em;
  color: $font-light-black;
  transition: color $duration-default;

  @media (max-width: $viewport-s) {
    font-size: 16px;
  }
}

.accordion__item {
  position: relative;
  border-bottom: 2px solid #F0F0F0;

  &.active {
    .accordion__content--feature {
      padding-top: 14px;
    }
  }
}

.accordion__content--feature {
  padding-top: 0;
  transition-duration: .3s;

  @media (max-width: $viewport-s) {
    font-size: 14px;
  }

  & > div {
    background-color: #F0F2F4;
    padding: 16px;
  }
}

.feature {
  .accordion__title {
    &:hover,
    &:focus {
      color: $color-gray;

      &::before,
      &::after {
        background-color: $color-gray;
      }
    }
  }
}
