.info {
  background: $bg-gray;

  &--white {
    background: white;
  }
}

.info__list {
  margin-left: 28px;
  font-size: 20px;
  color: $font-light-black;

  &--no-offset {
    margin: 0;
  }

  @media (max-width: $viewport-s) {
    margin-left: 0;
    font-size: 18px;
  }

  p {
    font-size: 18px;
    letter-spacing: .015em;
  }

  p + p {
    margin-top: 20px;
  }
}

.info--why .info__list {
  max-width: 750px;
}
