@mixin anim-shutter() {
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: $color-blue;
    transform: scale3d(0, 1, 1);
    transition-property: background-color, transform;
    transition-duration: $duration-default;
    transition-timing-function: $timing-default;
  }

  &:hover::before,
  &:focus::before {
    transform: scale3d(1, 1, 1);
  }
}

@keyframes buzz-out {
  10% {
    transform: scale(1.2) translateX(3px) rotate(10deg);
  }

  20% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg);
  }

  30% {
    transform: scale(1.2) translateX(3px) rotate(10deg);
  }

  40% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg);
  }

  50% {
    transform: scale(1.2) translateX(2px) rotate(5deg);
  }

  60% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg);
  }

  70% {
    transform: scale(1.2) translateX(2px) rotate(5deg);
  }

  80% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg);
  }

  90% {
    transform: scale(1.2) translateX(1px) rotate(0);
  }

  100% {
    transform: scale(1) translateX(-1px) rotate(0);
  }
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%,
  20% {
    transform: scale3d(0.7, 0.7, 0.7) rotate3d(0, 0, 1, -10deg);
  }

  30%,
  50%,
  70%,
  90% {
    transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, 10deg);
  }

  40%,
  60%,
  80% {
    transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, -10deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

//=============================

@keyframes bounceInRight {
  0% {
    visibility: visible;
  }

  20% {
    opacity: 1;
  }

  60% {
    transform: translate3d(-25px, 0, 0);
  }

  75% {
    transform: translate3d(10px, 0, 0);
  }

  90% {
    transform: translate3d(-5px, 0, 0);
  }

  100% {
    visibility: visible;
    opacity: 1;
    transform: none;
  }
}

@keyframes bounceOutRight {
  0% {
    visibility: visible;
    opacity: 1;
    transform: none;
  }

  40% {
    transform: translate3d(-50px, 0, 0);
  }

  80% {
    opacity: 1;
  }

  100% {
    visibility: hidden;
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
}
